:root{
  --color1: #7584E0;/*#542f99;*/
  --color2: #76aff7;/*#597bd6;*/
  --color3: #333399; 
}

small {
  font-size: .2em;
}

.section {
  padding-top: 15px;
}

.subtext {
  margin-left: 10px;
  font-size: .9em;
}

.App {
  margin-left: 10vw;
  margin-right: 10vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
}

@media (min-width: 1000px) {
  .App {
    width: 750px;
    margin-left: auto;
    margin-right: auto;
  }
}

.Header {
  height: 100px;
  overflow: visible;
}

nav {
  float: right;
  display: flex;
  justify-content: center;
}

.HeaderName {
  float: left;
  text-align: left;
  font-family: 'Major Mono Display', monospace;
  text-decoration: none;
  color: var(--color1);
  font-size: 3em;
  display: flex;
  justify-content: center;
}

header{
  font-weight: bold;
}
.current-tab a {
  color: var(--color1);
}
a {
  /*text-decoration: none;*/
  color:inherit;
  text-decoration: inherit;
  font-weight:bolder;
}

a:hover{
  color:var(--color2);
}

.content a{
  color:var(--color3);
  font-weight: bolder;
  font-size: 1.1em;
}

.content a:hover {
  color:var(--color2);
}

@media (max-width: 660px) {
  .HeaderName {
    float: none;
    padding-bottom: 1vw;
  }
  nav {
    float: none;
    align-items: center;
  }
  nav li a {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 350px) {
  .HeaderName {
    font-size: 10vw;
  }
}

nav ul {
  text-align: center; 
  padding-inline-start: 0px;
}
nav li {
  display: inline-block;
  font-family: 'Heebo', sans-serif;
  font-size: 1em;
  &:current {
    color: gray;
  }
}

nav li a {
  padding-left: 20px;
  padding-right: 20px;
  text-decoration: none;
  color: black;
}
.content {
  font-family: 'Heebo',sans-serif;
  padding-top: 20px;
}

.icon {
  padding: 10px 20px 10px 20px;
  display: inline-block;
}

.icons {
  display: block;
  text-align: center;
}
