
.about-img {
  width: 100%;
  height: auto;
  padding-bottom: 30px;
  display: inline-block;
  
}
.about-img img {
  width:100%;
  height: auto;
  -webkit-transition: 0.3s ease;
 transition: 0.3s ease;
}

.about-img img:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.about .media {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.about .caption {
  text-align: center;
}

.funnings {
  display: inline-block;
}
