
.projects .media {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.media iframe {
  min-width: 100%;
  max-width: 100%;
  height: 40vh;
  min-height: 200px;
  padding-bottom: 30px;
}

.projects .media img {
  height: auto;
  width: 100%;
  padding-bottom: 30px; 
}

.project .media .phoneimgsize {
  width:50%;
  height:auto;
  padding-bottom: 30px;
}

.projects .description {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects .description .icon {
  align-self: center;
}

.project {
  padding-bottom: 100px;
}
